import React from 'react';
import './Button.css';

const isOperator = function(value)
{
    return !isNaN(value) || value == "." || value == "=";
}

const Button = function(props)
{
    return(<div onClick={function() {props.handle(props.children)}.bind(this)} className={`button-wrapper ${isOperator(props.children) ? null : 'operator'}`}>{props.children}</div>);
}

export default Button;