import React from 'react';
import ReactDOM from 'react-dom';
import Button from './Button';
import Input from './Input';
import ClearButton from './ClearButton';
import * as math from 'mathjs';
import './index.css';

class App extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state = {input: ""};
        this.UpdateInput = this.UpdateInput.bind(this);
        this.HandleEqual = this.HandleEqual.bind(this);
    }

    UpdateInput(value)
    {
        this.setState({input: this.state.input + value});
        console.log(this.state.input);
    }

    HandleEqual()
    {
        this.setState({input: math.eval(this.state.input)});
    }

    render()
    {
        return(
            <div className="container">
                <div className="calc-wrapper">
                    <Input input={this.state.input}></Input>
                    <div className="row">
                        <Button handle={this.UpdateInput}>7</Button>
                        <Button handle={this.UpdateInput}>8</Button>
                        <Button handle={this.UpdateInput}>9</Button>
                        <Button handle={this.UpdateInput}>/</Button>
                    </div>
                    <div className="row">
                        <Button handle={this.UpdateInput}>6</Button>
                        <Button handle={this.UpdateInput}>5</Button>
                        <Button handle={this.UpdateInput}>4</Button>
                        <Button handle={this.UpdateInput}>X</Button>
                    </div>
                    <div className="row">
                        <Button handle={this.UpdateInput}>3</Button>
                        <Button handle={this.UpdateInput}>2</Button>
                        <Button handle={this.UpdateInput}>1</Button>
                        <Button handle={this.UpdateInput}>+</Button>
                    </div>
                    <div className="row">
                        <Button handle={this.UpdateInput}>.</Button>
                        <Button handle={this.UpdateInput}>0</Button>
                        <Button handle={this.HandleEqual}>=</Button>
                        <Button handle={this.UpdateInput}>-</Button>
                    </div>
                    <div className="row">
                        <ClearButton method={function() {this.setState({input: ""})}.bind(this)}>Clear</ClearButton>
                    </div>
                </div>
            </div>
        );
    }
}

ReactDOM.render(<App/>,document.getElementById("root"));